import React, { useState } from 'react';
import classNames from 'classnames'; //   

const Contact = () => {
    const [formData, setFormData] = useState({ name: '', email: '', message: '' });
    const [errors, setErrors] = useState({});
    const [successMessage, setSuccessMessage] = useState('');

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevData) => ({
            ...prevData,
            [name]: value,
        }));
    };

    const validate = () => {
        const newErrors = {};

        if (!formData.name) newErrors.name = ' ,  ';
        if (!formData.email) newErrors.email = ' ,   ';
        else if (!/\S+@\S+\.\S+/.test(formData.email)) newErrors.email = ' ,    ';

        if (!formData.message) newErrors.message = ' ,  ';

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (validate()) {
            console.log(' :', formData);
            setSuccessMessage('   !');
            setTimeout(() => setSuccessMessage(''), 3000); //    3 
            setFormData({ name: '', email: '', message: '' });
            setErrors({});
        }
    };

    return (
        <div className="contact-container">
            <h1></h1>
            <p>'      .</p>
            <form onSubmit={handleSubmit} className="contact-form">
                <div className="form-group">
                    <label>'</label>
                    <input
                        type="text"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        className={classNames('input', { 'input--error': errors.name })}
                    />
                    {errors.name && <span className="error-message">{errors.name}</span>}
                </div>
                <div className="form-group">
                    <label> </label>
                    <input
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        className={classNames('input', { 'input--error': errors.email })}
                    />
                    {errors.email && <span className="error-message">{errors.email}</span>}
                </div>
                <div className="form-group">
                    <label></label>
                    <textarea
                        name="message"
                        value={formData.message}
                        onChange={handleChange}
                        className={classNames('textarea', { 'textarea--error': errors.message })}
                    ></textarea>
                    {errors.message && <span className="error-message">{errors.message}</span>}
                </div>
                <button type="submit" className="button"> </button>
            </form>
            {successMessage && <p className="success-message">{successMessage}</p>} {/*     */}
        </div>
    );
};

export default Contact;
